<script>
  import { push } from "svelte-spa-router";
  import NoteEditor from "./components/NoteEditor.svelte";
  import { loadNotes, overwriteNote } from "./lib/storage";

  export let params = {};

  const note = loadNotes()[params.id];

  let title = note.title;
  let content = note.content;

  const onSave = () => {
    overwriteNote(params.id, { title, content });
    push("/");
  };
</script>

<style>
  .add {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .button-container {
    padding: 1em 0;
    text-align: right;
  }

  .save {
    background-color: rgb(62, 68, 163);
    border: none;
    border-radius: 3px;
    color: white;
    font-size: 1em;
    padding: 0.5em 1em;
    cursor: pointer;
  }

  .save:disabled {
    opacity: 0.3;
    cursor: auto;
  }
</style>

<div class="add">
  <NoteEditor bind:title bind:content />
  <div class="button-container">
    <button class="save" on:click={onSave} disabled={!title || !content}>
      Save
    </button>
  </div>
</div>
